import Vue from "vue";
import Vuex from "vuex";
import { query } from './modules/query';

import { Typing } from "@/utils/labels";
import { deepMerge } from "@/services/helper";

import { InjectionKey } from 'vue'
import { createStore, useStore as baseUseStore, Store } from 'vuex'

import * as dataService from "@/services/dataService";

// init default 
import { Label } from "@/utils/labels";
import * as _ from "lodash";
let nullVideoLevelLabel = {} as any;
Object.keys(Label.videoLevelLabels).forEach((x: string) => {
  nullVideoLevelLabel[x] = "";
});

let nullFrameLevelLabel = {} as any;
Object.keys(Label.frameLevelLabels).forEach((x: string) => {
  nullFrameLevelLabel[x] = "";
});

const nullVideoInfo: Typing.VideoInfo = {
  index: "",
  label: nullVideoLevelLabel as any,
  frames: [],
  transistion: []
}

// define your typings for the store state
export interface State {
  videoList: Typing.VideoList[];
  currentVideoId: string;
  currentVideoInfo: Typing.VideoInfo;
  videoPlaytime: number;
  selectedPeriod: [number, number];
  endtime: number;
  movementType: {
    type: any;
    tracking: any;
  }
}

// define injection key
export const key: InjectionKey<Store<State>> = Symbol()

export const store = createStore<State>({
  state: {
    // accessMapDict: {} as AccessMapDict<AccessMap>, //access, entropy, idx, type, zone
    videoList: [] as Typing.VideoList[],
    currentVideoId: "",
    currentVideoInfo: nullVideoInfo as Typing.VideoInfo,
    videoPlaytime: 0,
    selectedPeriod: [0, 0],
    endtime: -1,
    movementType: { type: "", tracking: "" }
  },
  modules: {
    query
  },
  getters: {
  },
  mutations: {
    setVideoList(state, videolist: Typing.VideoList[]) {
      state.videoList = videolist
    },
    setVideoPlaytime(state, videoPlaytime: number) {
      state.videoPlaytime = videoPlaytime;
    },
    setMovementType(state, movementType) {
      state.movementType = movementType;
    },
    setEndtime(state, endtime: number) {
      state.endtime = endtime;
    },
    setSelectedPeriod(state, selectedPeriod: [number, number]) {
      state.selectedPeriod = selectedPeriod;
    },
    setCurrentVideo(state, videoInfo: Typing.VideoInfo) {
      // set default value 
      if ('background' in videoInfo.label === false) {
        videoInfo.label = {
          ...nullVideoLevelLabel,
          ...videoInfo.label
        }
      }
      if (!Object.keys(videoInfo.frames[0].label).length) {
        videoInfo.frames.forEach((e: Typing.FrameInfo) => {
          e.label = {
            ...JSON.parse(JSON.stringify(nullFrameLevelLabel)),
            ...e.label,
          }
        })
      }

      // console.log(videoInfo);

      state.currentVideoInfo = videoInfo;
    },
    setVideoInfoTable(state, movement: Typing.Movement[]) {
      state.currentVideoInfo.transistion = movement;
    },
    changeCurrentVideo(state, change: DeepPartial<Typing.VideoInfo>) {
      dataService.emitChanges(change)
    },
    updateCurrentVideo(state, change: DeepPartial<Typing.VideoInfo>) {
      state.currentVideoInfo = _.merge(state.currentVideoInfo, change)
      // overwrite transition
      if ("transistion" in change) {
        state.currentVideoInfo.transistion = change.transistion as Required<Typing.Movement>[]
      }
      console.log(state.currentVideoInfo);
    }
  },
  actions: {
    updateFromServer({ state, commit }, change: DeepPartial<Typing.VideoInfo>) {
      console.log(change);
      if (change.index != undefined && change.index == state.currentVideoInfo.index) {
        commit('updateCurrentVideo', change)
      }
    },
    async init({ state, commit }, videoid: string) {
      let vl = await dataService.getVideoList();
      if ('comment' in vl[0] === false) {
        vl.forEach(x => x.comment = "")
      }
      commit('setVideoList', vl);
    },
    async setCurrentVideo({ state, commit }, videoid: string) {
      commit('setCurrentVideo', await dataService.getVideoById(videoid));
    },
    async saveCurrentVideo({ state, commit }, videoInfo: Typing.VideoInfo | undefined) {
      // if no input, assign to current
      videoInfo = videoInfo || state.currentVideoInfo;

      if (!videoInfo.index) return;

      // fetch from backend and merge
      let current = await dataService.getVideoById(videoInfo.index);
      let obj = deepMerge(videoInfo, current);

      // over-write
      commit('setCurrentVideo', obj);
      await dataService.saveCurrentVideo(obj);
    },
    async setVideoList({ state, commit }, videolist: Typing.VideoList[] | undefined) {
      videolist = videolist || state.videoList;
      commit('setVideoList', videolist);

      await dataService.saveVideoList(videolist);
    },
    /**
     * Used in Browser, search videos according to conditions
     * @param param0 
     * @param filter 
     */
    async onSearch({ state, commit }, filter) {
      let data = await dataService.queryVideo(filter);
      console.log(data)
    }
  },
  strict: process.env.NODE_ENV !== "production"
});

// define your own `useStore` composition function
export function useStore() {
  return baseUseStore(key)
}