
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";

import { Typing, Label } from "@/utils/labels";

import { onMounted, computed, reactive, toRefs, h, watch, toRaw } from "vue";
import { TreeDataItem } from "ant-design-vue/es/tree/Tree";
import { Dictionary } from "underscore";

import VideoList from "./VideoList.vue";
import LabelInterface from "./LabelInterface.vue";
import { cloneDeep } from "lodash";
import { CheckOutlined, EditOutlined } from "@ant-design/icons-vue";

const transitionTableConfig = {
    pagination: {
        defaultPageSize: 20,
        showQuickJumper: true,
        size: "small",
    },
    columns: [
        {
            title: "startframe",
            key: "startframe",
            dataIndex: "startframe",
            slots: { customRender: "startframe" },
        },
        {
            title: "endframe",
            key: "endframe",
            dataIndex: "endframe",
            slots: { customRender: "endframe" },
        },
        {
            title: "type",
            key: "type",
            dataIndex: "type",
            slots: { customRender: "type" },
        },
        {
            title: "tracking",
            key: "tracking",
            dataIndex: "tracking",
            slots: { customRender: "tracking" },
        },
        {
            title: "delete",
            dataIndex: "delete",
            slots: { customRender: "delete" },
        },
    ],
};

export default defineComponent({
    components: {
        CheckOutlined,
        EditOutlined,
    },
    setup() {
        const store = useStore();

        const state = reactive({
            table: computed({
                get: () =>
                    JSON.parse(
                        JSON.stringify(store.state.currentVideoInfo.transistion)
                    ) as (Typing.Movement & { [key: string]: string })[],
                set: () => {},
            }),
            formState: {
                startframe: 0,
                endframe: 0,
                type: [],
            } as unknown as Typing.Movement,
            videoPlaytime: computed(() => store.state.videoPlaytime),
            endtime: computed(() => store.state.endtime),
            selectedPeriod: computed(() => store.state.selectedPeriod),
            movementType: computed(() => store.state.movementType),
        });

        const editableData = reactive({} as { [key: string]: string });

        const edit = (key: string | number, field: string) => {
            console.log(key);

            // only allow one edit so clear all if there are more than 1
            if (Object.keys(editableData).length > 0) {
                for (var member in editableData) delete editableData[member];
            }
            editableData[key + field] = cloneDeep(
                state.table.filter((item) => key === item.startframe)[0]
            )[field];
            console.log("sdsad", editableData[key + field]);

            if (editableData[key + field] === undefined) {
                editableData[key + field] = "null";
            }
        };

        const save = (key: number, field: string) => {
            // console.log(
            //     key,
            //     state.table,
            //     state.table.filter((item) => item.startframe != key)
            // );
            Object.assign(state.table.filter((item) => key === item.startframe)[0], {
                [field]: editableData[key + field],
            });
            // store.commit("setVideoInfoTable", temp);
            store.commit("changeCurrentVideo", {
                index: store.state.currentVideoInfo.index,
                transistion: state.table,
            } as DeepPartial<Typing.VideoInfo>);
            //   Object.assign(state.table.filter(item => key === item.key)[0], editableData[key]);
            delete editableData[key + field];
        };

        watch(
            () => state.videoPlaytime,
            () => {
                state.formState.startframe = state.videoPlaytime + 0.5;
            }
        );

        watch(
            () => state.endtime,
            () => {
                state.formState.endframe = state.endtime;
            }
        );

        watch(
            () => state.movementType,
            () => {
                state.formState.type = state.movementType.type;
                state.formState.tracking = state.movementType.tracking;
            }
        )

        const onSubmit = () => {
            let current = toRaw(state.formState);
            // store.commit("setVideoInfoTable", [...state.table, current]);
            store.commit("changeCurrentVideo", {
                index: store.state.currentVideoInfo.index,
                transistion: [...state.table, current],
            } as DeepPartial<Typing.VideoInfo>);

            store.commit("setSelectedPeriod", [current.startframe, current.endframe]);
            // state.table.push(toRaw(state.formState));
            // console.log(state.table);

            // saveToStore();
        };

        const customRow = (record: any) => {
            return {
                onClick: () => {
                    // console.log(record);
                    store.commit("setSelectedPeriod", [record.startframe, record.endframe]);

                    store.commit("setMovementType", {type: record.type, tracking: record.tracking});
                },
            };
        };

        const saveToStore = () => {
            store.commit("setVideoInfoTable", state.table);
        };

        /**
         * On delete a row in transistion table
         */
        const onDeleteRow = (key: number) => {
            console.log(
                key,
                state.table,
                state.table.filter((item) => item.startframe != key)
            );
            let temp = state.table.filter((item) => item.startframe != key);
            // store.commit("setVideoInfoTable", temp);
            store.commit("changeCurrentVideo", {
                index: store.state.currentVideoInfo.index,
                transistion: temp,
            } as DeepPartial<Typing.VideoInfo>);
        };

        function onCellChange(key: number, dataIndex: string, value: string) {
            const dataSource = [...state.table];
            const target = dataSource.find((item) => item.startframe === key);
            if (target) {
                target[dataIndex] = value;
                state.table = dataSource;
            }
        }

        return {
            ...toRefs(state),
            onSubmit,
            onDeleteRow,
            transitionTableConfig,
            customRow,
            cameramovement: Label.cameramovement,
            tracking: Label.tracking,
            editableData,
            save,
            edit,
        };
    },
    computed: {},
});
