
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";

import { onMounted, computed, reactive, toRefs, h, watch, PropType } from "vue";
import { TreeDataItem } from "ant-design-vue/es/tree/Tree";
import { Dictionary } from "underscore";

export default defineComponent({
    name: "App",
    components: {},
    props: {
        treeData: {
            type: Array as PropType<TreeDataItem[]>,
        },
    },
    setup(props) {
        const store = useStore();

        const state = reactive({ expandedKeys: [], selectedKeys: [], checkedKeys: [] });

        const onSelect = (key: any, e: any) => {
            let ee = e.selectedNodes[0]?.props.title;
            if (ee) {
                window.location.hash = ee;
            }
        }

        return {
            ...toRefs(state),
            onSelect
        };
    },
    computed: {},
});
