
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent } from "vue";
import { mapState } from "vuex";
import { useStore } from "@/store/index";

import { onMounted, computed, reactive, toRefs, h, watch, PropType } from "vue";
import { TreeDataItem } from "ant-design-vue/es/tree/Tree";
import { Dictionary } from "underscore";

export default defineComponent({
    name: "VideoGallery",
    components: {},
    props: {
        leafNodeData: {
            type: Array as PropType<TreeDataItem[]>,
        },
    },
    setup(props) {
        const store = useStore();

        const state = reactive({
            queryResults: computed(() => (<any>store.state).query.queryResults),
        });

        const setVideo = (vIndex: string, fIndex: number) => {
            (<any>document.querySelector(`video#${vIndex}`)).currentTime = fIndex;
        };

        return {
            ...toRefs(state),
            setVideo,
        };
    },
    computed: {},
});
