import { Typing } from "@/utils/labels";
import * as _ from "underscore";

export function readTextFile(file: string) {
    var rawFile = new XMLHttpRequest();
    var allText = "";
    rawFile.open("GET", file, false);
    rawFile.onreadystatechange = function () {
        if (rawFile.readyState === 4) {
            if (rawFile.status === 200 || rawFile.status == 0) {
                allText = rawFile.responseText;
            }
        }
    };
    rawFile.send(null);
    return allText;
}

function merge<T extends Record<any, any>>(obj1: T, obj2: T) {
    const keys: (keyof T)[] = Object.keys(obj1)

    keys.forEach(k => {
        obj1[k] = obj1[k] || obj2[k]
    })
}
 
export function deepMerge(newer: Typing.VideoInfo, older: Typing.VideoInfo) {
    let obj = JSON.parse(JSON.stringify(newer)) as Typing.VideoInfo;

    // console.log(newer.transistion, older.transistion);

    merge(obj.label, older.label)
    obj.transistion = _.uniq([...older.transistion, ...obj.transistion], x => x.startframe);

    const olderFramesDict = _.groupBy(older.frames, x => x.index);
    obj.frames.forEach((value) => {
        let o = olderFramesDict[value.index][0];
        if (value.index !== o.index) {
            console.error('unexpected!')
        }
        merge(value.label, o.label);
    })

    // console.log(obj.transistion);

    return obj;
}