
import "bootstrap/dist/css/bootstrap.css";
import * as _ from "underscore";
import { SearchOutlined, ClearOutlined } from "@ant-design/icons-vue";

import { defineComponent } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";

import { onMounted, computed, reactive, toRefs, h, watch, PropType } from "vue";
import { TreeDataItem } from "ant-design-vue/es/tree/Tree";
import { Dictionary } from "underscore";

import { FilterLists } from "@/utils/labels";

const FilterOptions = FilterLists.filterOptions;
const FilterMise = _.pairs(FilterOptions["Mise-en-Scene"]).map((x) => ({
    field: x[0],
    options: x[1],
}));
const FilterCin = _.pairs(FilterOptions["Cinematography"]).map((x) => ({
    field: x[0],
    options: x[1],
}));

const MapName = new Map<string, string>([
    ["background", "Background"],
    ["anchor", "Anchor"],
    ["markType", "Mark Type"],
    ["alphaTrajectory", "Axis Trajectory"],
    ["betaEncoding", "Encoding"],
    ["markSemantic", "Mark Semantic"],
    ["animation", "Animation"],
    ["cameraTypeFlat", "Camera Movement"],
    ["purpose", "Purpose"],
    ["shotAngle", "Shot Angle"],
    ["shotDistance", "Shot Distance"]
]);

type FilterData = {
    [key in
        | keyof typeof FilterOptions["Mise-en-Scene"]
        | keyof typeof FilterOptions["Cinematography"]]: string | undefined;
};

type QueryIndices = ["vIndex", "startFrame", "endFrame"] | ["vIndex"];

export default defineComponent({
    name: "App",
    components: { SearchOutlined, ClearOutlined },
    props: {},
    setup(props) {
        const store = useStore();

        const state = reactive({
            filter: {} as FilterData,
            queryIndices: <QueryIndices>["vIndex"],
        });

        const onClear = () => {
            state.filter = <FilterData>{};
        };

        const onSearch = async () => {

            let filter = _.pick(state.filter, function (value, key, object) {
                return !!value;
            });

            let ifFilterByCamera = Object.keys(filter).some((x) =>
                [
                    "purpose",
                    "cameraTypeFlat",
                    "shotAngle",
                    "shotDistance",
                    // "cameraType",
                    // "angleSeq",
                    // "distanceSeq",
                ].includes(x)
            );
            state.queryIndices = ifFilterByCamera
                ? ["vIndex", "startFrame", "endFrame"]
                : ["vIndex"];

            await store.dispatch("query/onSearch", {
                selector: filter,
                index_cols: state.queryIndices,
            });
        };

        return {
            ...toRefs(state),
            FilterMise,
            FilterCin,
            MapName,
            onClear,
            onSearch,
        };
    },
    computed: {},
});
