import { createStore, useStore as baseUseStore, Store, Module } from 'vuex'
import * as dataService from "@/services/dataService";
import * as _ from "underscore";

// labelled data at frame-level
const allLabelPerFrame = require('@/assets/pro_data_results_with_url.json')

// get label data at video-level
const allLabelPerVideo = _.uniq(allLabelPerFrame, x => x.vIndex);

export type QueryFrame = {
    vIndex: string;
    fIndex: number;
    [x: string]: any;
}

export interface QueryVideo {
    vIndex: string;
    frames: QueryFrame[];
}

export const query: Module<any, any> = {
    namespaced: true,
    state: {
        queryResults: [] as QueryVideo[],
        allLabelPerFrame,
        allLabelPerVideo
    },
    modules: {
    },
    getters: {
    },
    mutations: {
        setQueryResults(state: any, queryResults: QueryVideo[]) {
            state.queryResults = queryResults
        }
    },
    actions: {
        /**
         * Used in Browser, search videos according to conditions
         * @param param0 
         * @param filter 
         */
        async onSearch({ state, commit }: any, filter: any) {
            var index_cols = filter['index_cols'].join("-");

            console.log(filter, index_cols);

            var selector = filter.selector; // selector is an obj, e.g., {'alphaBetaType': 'quantitative'}

            var searchFrom = (index_cols === 'vIndex') ? allLabelPerVideo : allLabelPerFrame;
            console.log(index_cols, searchFrom);

            var filtered = _.filter(searchFrom, function (item) {
                var matched = false;
                for (const [key, value] of Object.entries(selector)) {
                    if (item[key] === value) {
                        matched = true
                    } else {
                        matched = false;
                        break;
                    }
                }
                return matched;
            });

            // let data = await dataService.queryVideo(filter);
            let gps = _.groupBy(filtered, x => x.vIndex);
            let gpByVIndex = Object.keys(gps).map(x => ({
                'vIndex': x,
                'frames': gps[x],
                'link': gps[x][0].link
            }))

            console.log(gpByVIndex);
            commit('setQueryResults', gpByVIndex);
        }
    }
}