
import { onMounted, computed, reactive, toRefs, h, watch, UnwrapRef } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";

import { InboxOutlined, CheckOutlined, EditOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import { defineComponent, ref } from "vue";
import * as _ from "underscore";
import { Typing } from "@/utils/labels";

const sheetTableConfig = {
    pagination: {
        defaultPageSize: 20,
        showQuickJumper: true,
        size: "small",
    },
    columns: [
        {
            title: "index",
            key: "index",
            dataIndex: "index",
        },
        {
            title: "comment",
            dataIndex: "comment",
            slots: { customRender: "comment" },
        },
    ],
};

export default {
    components: { InboxOutlined, CheckOutlined, EditOutlined },
    setup() {
        const store = useStore();

        const state = reactive({
            videoList: computed(() => {
                let videoList = store.state.videoList;
                videoList = JSON.parse(JSON.stringify(videoList)) as Typing.VideoList[];
                return _.chain(videoList)
                    .sortBy((x) => parseInt(x.index))
                    .value();
            }),
        });

        const update = () => {
            store.dispatch("setVideoList", state.videoList);
        };

        const editableData: UnwrapRef<Record<string, Typing.VideoList>> = reactive({});

        const customRow = (record: any) => {
            return {
                onClick: () => {
                    // console.log(record);
                    store.dispatch("setCurrentVideo", record.index);
                },
            };
        };

        const edit = (key: string) => {
            editableData[key] = JSON.parse(
                JSON.stringify(state.videoList.filter((item) => key === item.index)[0])
            );
        };
        const save = (key: string) => {
            Object.assign(
                state.videoList.filter((item) => key === item.index)[0],
                editableData[key]
            );
            delete editableData[key];

            update();
        };

        return {
            sheetTableConfig,
            editableData,
            customRow,
            edit,
            save,
            ...toRefs(state),
        };
    },
};
