
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";

import { onMounted, computed, reactive, toRefs, h, watch } from "vue";
import { TreeDataItem } from "ant-design-vue/es/tree/Tree";
import { Dictionary } from "underscore";

import Tree from "./Tree.vue";
import List from "./List.vue";

const treeData = require("@/assets/taxonomy.json") as TreeDataItem[];

var leafNodeData = [] as TreeDataItem[];
const loopTree = (obj: TreeDataItem) => {
    if (!obj.title?.length) return false;

    let childern = obj.children;
    if (childern === undefined || childern.length === 0) {
        return true;
    }
    let loopChildren = childern.map((x) => loopTree(x));
    if (loopChildren.every((x) => x)) {
        leafNodeData.push(obj);
    }
    return false;
};
treeData.forEach((x) => loopTree(x));

console.log(leafNodeData);

export default defineComponent({
    name: "App",
    components: { Tree, List },
    setup() {
        const store = useStore();

        const state = reactive({ expandedKeys: [], selectedKeys: [], checkedKeys: [] });

        return {
            ...toRefs(state),
            treeData,
            leafNodeData,
        };
    },
    computed: {},
});
