
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";
import { Typing } from "@/utils/labels";

import { onMounted, computed, reactive, toRefs, h, watch, toRaw } from "vue";
import { TreeDataItem } from "ant-design-vue/es/tree/Tree";
import { Dictionary } from "underscore";
import DragSelect from "dragselect";

import { Label } from "@/utils/labels";

import TransitionTable from "./TransitionTable.vue";

export default defineComponent({
    components: { TransitionTable },
    setup() {
        const store = useStore();

        const state = reactive({
            currentVideoInfo: computed({
                get: () => {
                    let obj = store.state.currentVideoInfo;
                    obj = JSON.parse(JSON.stringify(obj));

                    return obj as Typing.VideoInfo;
                },
                set: (obj) => {
                    console.log("set");
                    store.commit("setCurrentVideo", obj);
                },
            }),
            videoPlaytime: computed(() => store.state.videoPlaytime),
            selectedPeriod: computed(() => store.state.selectedPeriod),
        });

        // watch the state.videoPlaytime to update
        watch(
            () => state.videoPlaytime,
            () => {
                let videoPlayer = document.querySelector("video");
                if (videoPlayer) videoPlayer.currentTime = state.videoPlaytime;
            }
        );

        const selectByFrameIDs = (start: number, end: number | undefined = undefined) => {
            end = end || start;

            if (start > end) return;

            dragSelector.clearSelection();
            var elements = [] as HTMLElement[];
            for (let id = start; id <= end; id++) {
                let e = document.querySelector(`div.frame[frameID="${id}"]`);
                elements.push(e as HTMLElement);
            }

            console.log(elements);
            
            dragSelector.addSelection(elements);
        };

        watch(
            () => state.selectedPeriod,
            () => {
                let v = state.selectedPeriod;
                console.log(v);

                if (v[0] < v[1]) {
                    selectByFrameIDs(v[0], v[1])
                }
            }
        );

        const changeLabel = (key: string, event: string) => {
            saveCurrentLabels({
                label: {
                    [key]: event
                }
            })
        }

        const saveCurrentLabels = (event: DeepPartial<Typing.VideoInfo>) => {
            event.index = state.currentVideoInfo.index;
            console.log("set label", event);
            
            store.commit("changeCurrentVideo", event)
            // store.commit("setCurrentVideo", state.currentVideoInfo);
        };

        const save = () => {
            store.dispatch("saveCurrentVideo", state.currentVideoInfo);
        };

        var dragSelector = new DragSelect({
            selectables: document.getElementsByClassName("frame"),
            draggability: false,
        } as any);

        watch(
            () => state.currentVideoInfo.index,
            () => {
                setTimeout(() => {
                    dragSelector.clearSelection();
                    dragSelector = new DragSelect({
                        selectables: document.querySelectorAll(".frame"),
                        area: document.querySelector("#frame-container"),
                        draggability: false,
                        multiSelectMode: true,
                    } as any);
                });
            }
        );

        dragSelector.subscribe("callback", () => {
            let sel = dragSelector.getSelection();
            if (sel.length) {
                console.log(sel);
            }
        });

        document.addEventListener("keyup", (e) => {
            if (e.code === "KeyC") {
                clearSelection();
            } else if (e.code === "KeyA") {
                selectByFrameIDs(state.selectedPeriod[0]);
            } else if (e.code === 'KeyS') {
                selectByFrameIDs(state.selectedPeriod[0] + 1, state.selectedPeriod[1] - 1);
            } else if (e.code === 'KeyD') {
                selectByFrameIDs(state.selectedPeriod[1]);
            } else if (e.code === 'KeyF') {
                selectByFrameIDs(state.selectedPeriod[0], state.selectedPeriod[1]);
            }
        });

        /**
         * On click the image event
         */
        const onClickImage = (e: any) => {
            console.log("clicked");
            
            let frameID = e.target.getAttribute("frameID");

            // // auto fill
            // state.formState.startframe = frameID;

            // set the video player
            let second = parseInt(frameID) - 0.5;
            store.commit("setVideoPlaytime", second);
        };

        const onRightClickImage = (e: any) => {
            let frameID = e.target.getAttribute("frameID");
            store.commit("setEndtime", frameID);
        };

        const clearSelection = () => {
            dragSelector.clearSelection();
        };

        // function write<K extends keyof A>(arg: A, key: K, value: A[K]): void {
        //     arg[key] = value;
        // }

        const assignLabel = (shotType: keyof Typing.FrameLabel, value: Label.ShotLevelLabel) => {
            console.log("assignLabel", dragSelector.getSelection());

            dragSelector.getSelection().forEach((el) => {
                let frameId = el.getAttribute("frameID");

                let tryFind = state.currentVideoInfo.frames.find((x) => x.index == frameId);
                if (tryFind != undefined) {
                    (tryFind.label[shotType] as any) = value;
                    saveCurrentLabels({
                        frames: state.currentVideoInfo.frames
                    });
                }
            });
        };

        const getLabelClass = (e: string[] | string) => {
            console.log(e, typeof e, typeof e == "string")
            if (typeof e == "string") {
                if (e.length === 0) return "todo";
            } else {
                if (e.some((x) => x === "")) {
                    return "todo";
                }
            }
        };

        const onRangeImage = (e: any) => {
            console.log("end selection using shift");
            selectByFrameIDs(parseInt(dragSelector.getSelection()[0].getAttribute("frameID")!), parseInt(e.target.getAttribute("frameID")))
            // store.commit("setSelectedPeriod", [state.selectedPeriod[0], parseInt(e.target.getAttribute("frameID"))])
        }

        return {
            ...toRefs(state),
            videoLevelLabels: Label.videoLevelLabels,
            frameLevelLabels: Label.frameLevelLabels,
            saveCurrentLabels,
            save,
            onClickImage,
            clearSelection,
            assignLabel,
            getLabelClass,
            onRightClickImage,
            changeLabel,
            onRangeImage
        };
    },
    computed: {},
});
