export namespace Typing {
    export type VideoInfo = {
        index: string;
        label: {
            background: Label.Background | undefined;
            prop: Label.Prop | undefined;
            marktype: Label.MarkType | undefined;
            alphadatatype: Label.AlphaDataType | undefined;
            alphatrajectory: Label.AlphaTrajectory | undefined;
            alphaSpace: Label.AlphaSpace | undefined;
            betadatatype: Label.BetaDataType | undefined;
            betaencoding: Label.BetaEncoding | undefined;
            betareallife: Label.BetaReallife | undefined;
            marksemantic: Label.MarkSemantic | undefined;
            composition: Label.Composition | undefined;
            animation: Label.Animation | undefined;
            [key: string]: string | undefined
        },
        frames: FrameInfo[];
        transistion: Movement[];
    }

    export type FrameInfo = {
        index: string;
        label: FrameLabel;
    }

    export type FrameLabel = {
        // shotframe: Label.ShotFrame,
        shotangle: Label.ShotAngle,
        shotdistance: Label.ShotDistance
    }

    export type Movement = {
        startframe: number;
        endframe: number;
        type: string[];
        tracking: string[];
    }

    export type VideoList = {
        link: string;
        likes: number;
        index: string;
        filename: string;
        comment: string;
    }
}

